/* src/components/LNB/LNB.css */
.lnb {
    width: 280px;
    height: calc(100vh - 64px);
    background-color: #ffffff;
    border-right: 1px solid #e9ecef;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
  }
  
  /* 프로필 카드 */
  .lnb-profile-card {
    padding: 20px;
    border-bottom: 1px solid #e9ecef;
    background-color: #f8f9fa;
  }
  
  .profile-image {
    width: 60px;
    height: 60px;
    background-color: #e9ecef;
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 12px;
  }
  
  .profile-icon {
    font-size: 28px;
  }
  
  .profile-info {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  
  .profile-name {
    font-size: 18px;
    font-weight: 700;
    color: #212529;
    margin: 0;
    cursor: pointer;
    transition: color 0.2s;
  }
  
  .profile-name:hover {
    color: #4852EA;
  }
  
  .profile-details {
    font-size: 13px;
    color: #6c757d;
    line-height: 1.4;
  }
  
  .profile-details p {
    margin: 0;
  }
  
  .profile-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
    margin-top: 8px;
  }
  
  .tag {
    padding: 4px 8px;
    font-size: 12px;
    border-radius: 4px;
    font-weight: 500;
  }
  
  .tag.primary {
    background-color: #4852EA;
    color: white;
  }
  
  .tag.secondary {
    background-color: #e9ecef;
    color: #495057;
  }
  
  .tag.link {
    background-color: #e3f2fd;
    color: #1976d2;
    cursor: pointer;
  }
  
  .tag.link:hover {
    background-color: #bbdefb;
  }
  
  /* 메뉴 */
  .lnb-menu {
    list-style: none;
    padding: 16px 12px;
    margin: 0;
    flex: 1;
  }
  
  .lnb-menu-item {
    margin-bottom: 4px;
  }
  
  .lnb-menu-item > a,
  .lnb-menu-item > .disabled-menu {
    display: flex;
    align-items: center;
    padding: 12px 16px;
    color: #495057;
    text-decoration: none;
    border-radius: 8px;
    transition: all 0.2s;
    font-weight: 500;
  }
  
  .lnb-menu-item > a:hover {
    background-color: #f8f9fa;
    color: #4852EA;
  }
  
  .lnb-menu-item > a.active {
    background-color: #4852EA;
    color: white;
  }
  
  /* 비활성화된 메뉴 */
  .lnb-menu-item > .disabled-menu {
    color: #adb5bd;
    cursor: not-allowed;
    opacity: 0.6;
  }
  
  .lnb-menu-item .icon {
    margin-right: 12px;
    font-size: 18px;
  }
  
  .lnb-menu-item .label {
    font-size: 15px;
  }
  
  /* 서브메뉴 */
  .lnb-submenu {
    list-style: none;
    padding: 4px 0 8px 48px;
    margin: 0;
  }
  
  .lnb-submenu li {
    margin-bottom: 2px;
  }
  
  .lnb-submenu a,
  .lnb-submenu .disabled-submenu {
    display: block;
    padding: 8px 16px;
    color: #6c757d;
    text-decoration: none;
    font-size: 14px;
    border-radius: 6px;
    transition: all 0.2s;
  }
  
  .lnb-submenu a:hover {
    color: #4852EA;
    background-color: #f8f9fa;
  }
  
  .lnb-submenu a.sub-active {
    color: #4852EA;
    font-weight: 600;
  }
  
  /* 비활성화된 서브메뉴 */
  .lnb-submenu .disabled-submenu {
    color: #ced4da;
    cursor: not-allowed;
    opacity: 0.5;
  }
  
  .lnb-submenu li.disabled {
    opacity: 0.5;
  }
  
  /* 스크롤바 스타일 */
  .lnb::-webkit-scrollbar {
    width: 6px;
  }
  
  .lnb::-webkit-scrollbar-track {
    background: #f8f9fa;
  }
  
  .lnb::-webkit-scrollbar-thumb {
    background: #dee2e6;
    border-radius: 3px;
  }
  
  .lnb::-webkit-scrollbar-thumb:hover {
    background: #adb5bd;
  }
  
  /* 파트너 타입 표시 (선택사항) */
  .profile-name::after {
    content: ' ▼';
    font-size: 12px;
    color: #6c757d;
    margin-left: 4px;
  }