/* src/components/Layout/Layout.css */
.app-layout {
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #f8f9fa;
    overflow: hidden;
  }
  
  .app-body {
    display: flex;
    flex: 1;
    height: calc(100vh - 64px);
    overflow: hidden;
  }
  
  .app-content {
    flex: 1;
    padding: 24px;
    overflow-y: auto;
    background-color: #f8f9fa;
    height: 100%;
  }
  
  /* 스크롤바 스타일 */
  .app-content::-webkit-scrollbar {
    width: 8px;
  }
  
  .app-content::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  .app-content::-webkit-scrollbar-thumb {
    background: #c1c1c1;
    border-radius: 4px;
  }
  
  .app-content::-webkit-scrollbar-thumb:hover {
    background: #a8a8a8;
  }
  
  /* 반응형 */
  @media (max-width: 768px) {
    .app-body {
      flex-direction: column;
      height: auto;
    }
    
    .lnb {
      width: 100%;
      height: auto;
      border-right: none;
      border-bottom: 1px solid #e9ecef;
    }
    
    .app-content {
      height: auto;
    }
  }