/* src/pages/c-home-01/c-home-01.css */

.dashboard-container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  /* 날짜 선택 */
  .date-selector {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 32px;
    gap: 16px;
  }
  
  .date-selector h2 {
    font-size: 24px;
    font-weight: 600;
    color: #212529;
    margin: 0;
    min-width: 250px;
    text-align: center;
  }
  
  .date-arrow {
    width: 36px;
    height: 36px;
    border: 1px solid #e9ecef;
    background: white;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.2s;
    font-size: 14px;
    color: #495057;
  }
  
  .date-arrow:hover {
    border-color: #4852EA;
    color: #4852EA;
    background-color: #f8f9fa;
  }
  
  /* 핵심 지표 카드 */
  .metric-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 20px;
    margin-bottom: 32px;
  }
  
  .metric-card {
    background: white;
    border-radius: 12px;
    padding: 24px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
    border: 1px solid #e9ecef;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .metric-card:hover {
    box-shadow: 0 4px 16px rgba(72, 82, 234, 0.1);
    border-color: #4852EA;
    transform: translateY(-2px);
  }
  
  .metric-card h3 {
    font-size: 16px;
    font-weight: 500;
    color: #6c757d;
    margin: 0 0 12px 0;
  }
  
  .metric-value {
    font-size: 32px;
    font-weight: 700;
    color: #212529;
  }
  
  .new-reservation .metric-value {
    color: #4852EA;
  }
  
  .today-visit .metric-value {
    color: #28a745;
  }
  
  /* 섹션 공통 스타일 */
  .today-tasks,
  .new-inquiries {
    background: white;
    border-radius: 12px;
    padding: 24px;
    margin-bottom: 24px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
    border: 1px solid #e9ecef;
  }
  
  .section-header {
    margin-bottom: 20px;
    padding-bottom: 16px;
    border-bottom: 1px solid #e9ecef;
  }
  
  .section-header h3 {
    font-size: 18px;
    font-weight: 600;
    color: #212529;
    margin: 0;
  }
  
  .section-footer {
    margin-top: 16px;
    padding-top: 16px;
    border-top: 1px solid #e9ecef;
    text-align: right;
  }
  
  .more-link {
    color: #4852EA;
    text-decoration: none;
    font-size: 14px;
    font-weight: 500;
    transition: all 0.2s;
  }
  
  .more-link:hover {
    text-decoration: underline;
  }
  
  /* 오늘의 할 일 */
  .task-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }
  
  .task-item {
    display: flex;
    align-items: center;
    padding: 12px;
    background-color: #f8f9fa;
    border-radius: 8px;
    transition: all 0.2s;
    cursor: pointer;
  }
  
  .task-item:hover {
    background-color: #e9ecef;
  }
  
  .task-time {
    font-weight: 600;
    color: #495057;
    margin-right: 16px;
    min-width: 50px;
  }
  
  .task-type {
    padding: 4px 12px;
    border-radius: 4px;
    font-size: 12px;
    font-weight: 600;
    margin-right: 16px;
  }
  
  .task-type.maintenance {
    background-color: #e3f2fd;
    color: #1976d2;
  }
  
  .task-type.inspection {
    background-color: #f3e5f5;
    color: #7b1fa2;
  }
  
  .task-customer {
    font-weight: 500;
    color: #212529;
    margin-right: 8px;
  }
  
  .task-service {
    color: #6c757d;
    flex: 1;
  }
  
  /* 신규 문의 */
  .inquiry-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }
  
  .inquiry-item {
    padding: 16px;
    background-color: #f8f9fa;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .inquiry-item:hover {
    background-color: #e9ecef;
  }
  
  .inquiry-type {
    display: inline-block;
    color: #4852EA;
    font-weight: 600;
    margin-right: 12px;
  }
  
  .inquiry-content {
    color: #495057;
  }
  
  /* 반응형 */
  @media (max-width: 768px) {
    .date-selector h2 {
      font-size: 20px;
      min-width: auto;
    }
    
    .metric-cards {
      grid-template-columns: 1fr;
    }
    
    .task-item {
      flex-wrap: wrap;
    }
    
    .task-service {
      width: 100%;
      margin-top: 8px;
    }
  }