/* src/pages/c-log/c-log.css */

/* 로그인 화면 스타일 - 블루 테마 */
.c-log-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f8f9fa;
}

.c-log-container {
  background: white;
  padding: 48px 40px;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(72, 82, 234, 0.1);
  width: 100%;
  max-width: 400px;
}

.c-log-logo {
  text-align: center;
  margin-bottom: 40px;
}

.c-log-logo h1 {
  color: #4852EA;
  font-size: 32px;
  font-weight: 700;
  margin: 0;
  letter-spacing: -0.5px;
}

.c-log-form {
  width: 100%;
}

.c-log-input-group {
  margin-bottom: 20px;
}

.c-log-input-group label {
  display: block;
  margin-bottom: 8px;
  color: #495057;
  font-size: 14px;
  font-weight: 600;
}

.c-log-input-group input {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  font-size: 16px;
  transition: all 0.2s;
  box-sizing: border-box;
}

.c-log-input-group input:focus {
  outline: none;
  border-color: #4852EA;
  box-shadow: 0 0 0 3px rgba(72, 82, 234, 0.1);
}

.c-log-input-group input::placeholder {
  color: #adb5bd;
}

.c-log-checkbox {
  margin-bottom: 24px;
  display: flex;
  align-items: center;
}

.c-log-checkbox input[type="checkbox"] {
  margin-right: 8px;
  width: 18px;
  height: 18px;
  cursor: pointer;
  accent-color: #4852EA;
}

.c-log-checkbox label {
  color: #6c757d;
  font-size: 14px;
  cursor: pointer;
  user-select: none;
}

.c-log-submit {
  width: 100%;
  padding: 14px;
  background-color: #4852EA;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  box-shadow: 0 2px 8px rgba(72, 82, 234, 0.2);
}

.c-log-submit:hover {
  background-color: #3942d9;
  box-shadow: 0 4px 12px rgba(72, 82, 234, 0.3);
  transform: translateY(-1px);
}

.c-log-submit:active {
  background-color: #2a32c8;
  transform: translateY(0);
}

.c-log-links {
  text-align: center;
  margin-top: 24px;
  font-size: 14px;
}

.c-log-links a {
  color: #6c757d;
  text-decoration: none;
  transition: color 0.2s;
  font-weight: 500;
}

.c-log-links a:hover {
  color: #4852EA;
  text-decoration: underline;
}

.c-log-divider {
  margin: 0 12px;
  color: #dee2e6;
}

/* 반응형 디자인 */
@media (max-width: 768px) {
  .c-log-container {
      padding: 40px 24px;
      margin: 20px;
  }
  
  .c-log-logo h1 {
      font-size: 28px;
  }
}