/* src/pages/c-join/c-join.css */

.c-join-wrapper {
  min-height: 100vh;
  background-color: #f8f9fa;
  padding: 40px 20px;
}

.c-join-container {
  max-width: 600px;
  margin: 0 auto;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(72, 82, 234, 0.08);
  padding: 48px;
}

/* 헤더 */
.c-join-header {
  text-align: center;
  margin-bottom: 40px;
}

.c-join-header h1 {
  color: #4852EA;
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 8px;
  cursor: pointer;
}

.c-join-header h2 {
  color: #212529;
  font-size: 24px;
  font-weight: 600;
}

/* 폼 섹션 */
.form-section {
  margin-bottom: 40px;
  padding-bottom: 40px;
  border-bottom: 1px solid #e9ecef;
}

.form-section:last-of-type {
  border-bottom: none;
  margin-bottom: 32px;
  padding-bottom: 0;
}

.form-section h3 {
  color: #212529;
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 20px;
  padding-bottom: 12px;
  border-bottom: 2px solid #4852EA;
}

/* 폼 그룹 */
.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  color: #495057;
  font-size: 14px;
  font-weight: 600;
}

.form-group input[type="text"],
.form-group input[type="password"],
.form-group input[type="email"],
.form-group input[type="tel"],
.form-select {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  font-size: 16px;
  transition: all 0.2s;
}

.form-group input:focus,
.form-select:focus {
  outline: none;
  border-color: #4852EA;
  box-shadow: 0 0 0 3px rgba(72, 82, 234, 0.1);
}

/* 버튼이 있는 입력 필드 */
.form-group.with-button .input-with-button {
  display: flex;
  gap: 8px;
}

.input-with-button input {
  flex: 1;
}

.btn-verify,
.btn-search {
  padding: 12px 20px;
  background-color: #6c757d;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  white-space: nowrap;
  transition: all 0.2s;
}

.btn-verify:hover,
.btn-search:hover {
  background-color: #5a6268;
}

/* 파일 입력 */
.form-group input[type="file"] {
  width: 100%;
  padding: 10px;
  border: 2px dashed #e9ecef;
  border-radius: 8px;
  background-color: #f8f9fa;
  cursor: pointer;
}

/* 약관 동의 */
.agree-all {
  padding: 16px;
  background-color: #f8f9fa;
  border-radius: 8px;
  margin-bottom: 16px;
}

.agree-all label {
  display: flex;
  align-items: center;
  font-weight: 600;
  cursor: pointer;
}

.agree-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.agree-list label {
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
  padding-left: 16px;
}

.agree-list input[type="checkbox"],
.agree-all input[type="checkbox"] {
  width: 18px;
  height: 18px;
  margin-right: 10px;
  cursor: pointer;
  accent-color: #4852EA;
}

.agree-list span {
  flex: 1;
  color: #495057;
  font-size: 14px;
}

.agree-list a {
  color: #4852EA;
  font-size: 14px;
  text-decoration: underline;
  margin-left: 8px;
}

/* 버튼 영역 */
.form-buttons {
  display: flex;
  gap: 12px;
  margin-top: 40px;
}

.btn-cancel,
.btn-submit {
  flex: 1;
  padding: 16px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-cancel {
  background-color: #e9ecef;
  color: #495057;
}

.btn-cancel:hover {
  background-color: #dee2e6;
}

.btn-submit {
  background-color: #4852EA;
  color: white;
  box-shadow: 0 2px 8px rgba(72, 82, 234, 0.2);
}

.btn-submit:hover {
  background-color: #3942d9;
  box-shadow: 0 4px 12px rgba(72, 82, 234, 0.3);
  transform: translateY(-1px);
}

/* 유틸리티 클래스 */
.mt-8 {
  margin-top: 8px;
}

/* 반응형 */
@media (max-width: 768px) {
  .c-join-container {
    padding: 32px 24px;
  }
  
  .form-section h3 {
    font-size: 16px;
  }
  
  .form-buttons {
    flex-direction: column;
  }

  /* 약관관동의 */
.btn-link {
  background: none;
  border: none;
  color: #4852EA;
  font-size: 14px;
  text-decoration: underline;
  cursor: pointer;
  padding: 0;
  margin-left: 8px;
}

.btn-link:hover {
  color: #3942d9;
}
}